import React, { useEffect, useState } from "react";
import Footer from "../components/Footer";
import {
  RecipeComment,
  RecipeWriteTop,
  RecipeWriteWrap,
  TextBoxes,
  WriteButtons,
} from "../styles/write/recipewriteStyle";
import AddImages from "../components/write/AddImages";
import { postMeal } from "../api/write/write_api";
import { getDownloadURL, ref, uploadBytes } from "firebase/storage";
import { storage } from "../fb/firebaseconfig";
import AddTags from "../components/write/Addtags";
import { useNavigate } from "react-router";

const initData = {
  title: "",
  ingredient: "",
  recipe: "",
  review: "",
  pics: [""],
  tags: [""],
};
const RecipeWrite = () => {
  const [title, setTitle] = useState("");
  const [ingredient, setIngredient] = useState("");
  const [recipe, setRecipe] = useState("");
  const [review, setReview] = useState("");
  const [pics, setPics] = useState([]);
  const [songTags, setSongTags] = useState([]);
  const navigate = useNavigate();

  //   ( )
  const handleChangeTitle = e => {
    setTitle(e.target.value);
  };
  const handleChangeIngredient = e => {
    setIngredient(e.target.value);
  };
  //    .
  const handleChangeTags = updatedTags => {
    setSongTags(updatedTags);
  };
  const handleChangeRecipe = e => {
    setRecipe(e.target.value);
  };
  const handleChangeReview = e => {
    setReview(e.target.value);
  };

  //    ( )
  const handelClickReset = () => {
    setTitle("");
    setIngredient("");
    setSongTags("");
    setRecipe("");
    setPics([]);
    setReview("");
  };
  //  
  //      
  //     .
  const handelClickSubmit = () => {
    if (title === "") {
      alert(" .");
      return;
    }
    if (ingredient === "") {
      alert(" .");
      return;
    }
    // if (songTagsResult.length == 5) {
    //   alert("#  1  .");
    //   return;
    // }
    if (recipe === "") {
      alert(" .");
    }
    if (uploadedImage === null) {
      alert(" .");
      return;
    }
    if (review === "") {
      alert("comment .");
      return;
    }

    const obj = {
      title: title,
      ingredient: ingredient,
      recipe: recipe,
      review: review,
      picIdx: [0, 1],
      pics: pics,
      tagIdx: [0, 1, 2, 3, 4],
      tags: songTags,
    };
    postMeal(obj, error500);
  };

  const error500 = () => {
    navigate("/meal/write")
  } 

  //    state
  const [uploadedImage, setUploadedImage] = useState(null);

  // AddImages       
  const handleImageUpload = async file => {
    const imageUrl = await uploadImageToStorage(file);

    //  pics         
    if (!pics.includes(imageUrl)) {
      setPics(prevPics => [...prevPics, imageUrl]);
    }
    setUploadedImage(imageUrl);
  };

  //   
  const uploadImageToStorage = async file => {
    try {
      // Firebase Storage    (: images  )
      const storageRef = ref(storage, `images/${file.name}`);

      //  Storage    
      const fbRes = await uploadBytes(storageRef, file);

      //    URL 
      const imageUrl = await getDownloadURL(fbRes.ref);

      //   URL 
      return imageUrl;
    } catch (error) {
      console.error(error);
      //   (:   )
      throw error;
    }
  };

  const successWrite = () => {
    alert(" .");
    navigate("/meal");
  };

  //  .
  useEffect(() => {}, []);

  return (
    <div>
      <RecipeWriteWrap>
        <RecipeWriteTop>
          {/*   */}
          <AddImages
            onImageUpload={(file, index) => handleImageUpload(file, index)}
            setPics={setPics}
            imageUrl={pics}
          ></AddImages>
          {/*   */}
          <TextBoxes>
            <input
              className="textboxes-title"
              placeholder=" ."
              value={title}
              onChange={e => handleChangeTitle(e)}
            />
            <textarea
              className="textboxes-ingre"
              placeholder=" ."
              value={ingredient}
              onChange={e => handleChangeIngredient(e)}
            />
            {/*   */}
            <AddTags onTagsUpdate={handleChangeTags}></AddTags>
            {/*      */}
            <RecipeComment>
              <textarea
                className="textboxes-recipe"
                placeholder=" ."
                value={recipe}
                onChange={e => handleChangeRecipe(e)}
              />
              <textarea
                className="textboxes-comment"
                placeholder="comment ."
                value={review}
                onChange={e => handleChangeReview(e)}
              />
            </RecipeComment>
          </TextBoxes>
          {/*  2 */}
          <WriteButtons>
            <button
              type="reset"
              className="restart-bt"
              onClick={e => {
                handelClickReset(e);
              }}
            >
              <img src="/images/write/reset_icon.png" alt="" />
            </button>
            <button
              type="button"
              className="complete-bt"
              onClick={e => {
                handelClickSubmit(e);
                successWrite();
              }}
            >
              <img src="/images/write/check_icon.svg" alt="" />
            </button>
          </WriteButtons>
          <Footer></Footer>
        </RecipeWriteTop>
      </RecipeWriteWrap>
    </div>
  );
};

export default RecipeWrite;
