import { Button, Modal } from "antd";
import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import { PostUserS } from "../api/user/signup_api";
import {
  ButtonLog,
  LogBox,
  LogBoxs,
  Sign,
  Wraps,
} from "../styles/myuser/mylog";

const LogIn = () => {
  const [uid, setUid] = useState("");
  const [upw, setUpw] = useState("");
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [modalContent, setModalContent] = useState("");
  const Navigate = useNavigate();

  const goRouter = e => {
    e.preventDefault();
    const objs = {
      uid: uid,
      upw: upw,
    };

    if (!uid || !upw) {
      setModalContent("  .");
      setIsModalOpen(true);
      return;
    }

    PostUserS(objs, successPostUserS, failPostUserS);
  };

  const successPostUserS = _data => {
    if (_data.result === 1) {
      setIsModalOpen(true);
      setModalContent(`${uid}  .`);
      Navigate("/meal");
    } else if (_data.result === 2) {
      setModalContent("  .");
      setIsModalOpen(true);
    } else if (_data.result === 3) {
      setModalContent("  .");
      setIsModalOpen(true);
    }
  };

  const failPostUserS = () => {
    setModalContent("  .");
    setIsModalOpen(true);
  };

  const handleOk = () => {
    setIsModalOpen(false);
  };

  return (
    <Wraps>
      <LogBoxs>
        <LogBox>
          <img src="../../images/main_logo.svg" alt=""></img>
          <label className="user">
            <input
              type="text"
              name="username"
              value={uid}
              onChange={e => setUid(e.target.value)}
              placeholder=""
              className="id"
            />
          </label>

          <label className="user">
            <input
              type="password"
              value={upw}
              name="usernam"
              onChange={e => setUpw(e.target.value)}
              placeholder=""
              className="pw"
            />
          </label>
        </LogBox>
        <ButtonLog onClick={e => goRouter(e)}></ButtonLog>
        <Link to={"/meal/signup"}>
          <Sign></Sign>
        </Link>
        <Modal
          className="ant-modal-content"
          open={isModalOpen}
          onOk={handleOk}
          closable={false}
          style={{ textAlign: "center" }}
          footer={[
            <Button key="submit" type="primary" onClick={handleOk}>
              OK
            </Button>,
          ]}
        >
          <p>{modalContent}</p>
        </Modal>
      </LogBoxs>
    </Wraps>
  );
};

export default LogIn;
