import axios from "axios";

//   
// imeal: pk, title: ( ), ingredient: , recipe: , review: 
// picIdx:  (List), pics:   (List),
// tagIdx:  (List), tags:   (List)
// (result(-4):  , ,  , (0): , (1): )

//  () 
export const getMoreSong = async (imeal, successMoreData, error500) => {
  try {
    const url = `/api/meal/${imeal}`;
    const res = await axios.get(url);
    successMoreData(res.data);
  } catch (error) {
    console.log(error);
    error500();
  }
};

//   (put)
export const putMeal = async (obj, successEdit, error500) => {
  console.log("putMeal", obj);
  try {
    const res = await axios.put("/api/meal", obj);
    // console.log(res.status);
    console.log(res.data);
    //
    const situation = res.status.toString();

    if (situation.charAt(0) === "2") {
      // 
      successEdit();
    } else if (situation.charAt(0) === "4") {
      alert(
        "       ,    ",
      );
    }
  } catch (error) {
    //   
    console.log(error);
    error500();
  }
};

