//   (post)
export const postMeal = async (obj, error500) => {
  try {
    const res = await axios.post("/api/meal", obj);
    // console.log(" ", res.data);
  } catch (error) {
    console.log(error);
    error500();
  }
};

export const deleteMealTag = async (itag, error500) => {
  console.log("deleteMealTag", itag);
  try {
    const res = await axios.delete(`/api/meal/tag?itag=${itag}`);
    if (res.data === 0) {
      alert(" ");
      //
    } else if (res.data === 1) {
      alert(" ");
      //
    }
  } catch (error) {
    //   
    console.log(error);
    error500();
  }
};

// (result(0): , (1): , (-1):     ,
//     )

export const deleteMealPic = async (imeal, ipic) => {
  console.log("deleteMealPic", " imeal=", imeal, " ipic=", ipic);
  try {
    //?imeal=0&ipic=0
    const res = await axios.delete(`/api/meal/pic?imeal=${imeal}&ipic=${ipic}`);
    if (res.data === 0) {
      alert(" ");
      //
    } else if (res.data === 1) {
      alert(" ");
      //
    } else if (res.data === -1) {
      alert(" 1 ");
      //
    }
  } catch (error) {
    //   
    console.log(error);
    window.location.href = "/";
  }
};
